.page {
    .image-hero-background.absolute {
        position: fixed;
        width: 100vw;
        height: 100vh;
        z-index: -1;

        background-position: center;
        background-size: cover;

        &::before {
            content: '';
            position: absolute;
            width: 100vw;
            height: 100vh;
            background-color: rgba(#000, .2);
        }
    }
}

// Extra small devices (portrait phones, less than 576px)
